<template>
  <div class="DataTable">
    <div class="tableMain">
    <el-table
      :data="tableData5"
      style="width: 100%">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.shop }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.id }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.shopId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.category }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.address }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.desc }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        label=" ID"
        prop="id">
      </el-table-column>
      <el-table-column
        label=""
        prop="name">
      </el-table-column>
      <el-table-column
        label=""
        prop="desc">
      </el-table-column>
        <el-table-column
          class-name="show"
          label=" ID"
          prop="id">
        </el-table-column>
    </el-table>
    </div>
    <div class="tableBut">
      <el-pagination
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData5: [{
        id: '12987122',
        name: '',
        category: '',
        desc: '',
        address: '',
        shop: '',
        shopId: '10333'
      }, {
        id: '12987123',
        name: '',
        category: '',
        desc: '',
        address: '',
        shop: '',
        shopId: '10333'
      }, {
        id: '12987125',
        name: '',
        category: '',
        desc: '',
        address: '',
        shop: '',
        shopId: '10333'
      }, {
        id: '12987126',
        name: '',
        category: '',
        desc: '',
        address: '',
        shop: '',
        shopId: '10333'
      }],
      screenWidth: document.body.clientWidth,
      show: 0
    }
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
      })()
    }
  },
  watch: {
    screenWidth (val) {
      this.screenWidth = val
      if (this.screenWidth < 800) {
        this.show = 0
        console.log(this.screenWidth)
      }
    }
  }
}

</script>

<style scoped>
  .DataTable{
    padding:20px;
  }
  .tableBut{
    border:1px solid #dfe6ec;
    border-top:0px;
    padding:15px;
    text-align: right;
    background: #fff;
  }
  .show{
    display: none;
  }
  
</style>



